.form {
  border: 1px solid rgba($textLight, 0.3);
  padding: 1.5rem;
  border-radius: 4px;
}
.form-legend {
  font-size: 2.5rem;
  color: $textDark;
}
.form-switch {
  color: $primary;
  font-size: 1rem;
  &:hover {
    text-decoration: underline;
  }
}
.form-control {
  display: block;
  width: 100%;
  font-size: 1.1rem;
  border: 1px solid rgba($textLight, 0.7);
  padding: 0.75rem 1.2rem;
  border-radius: 4px;
  margin-bottom: 1.5rem;
  color: $textLight;
  &:focus {
    border: 1px solid $textDark;
  }
}
span.error {
  color: red;
  display: block;
  text-align: center;
  padding-top: 5px;
}

.form-group {
  margin-top: 1rem;
}
.logout-btn {
  margin-top: 2rem;
}

input:disabled,
input[disabled] {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
  cursor: not-allowed;
}
